import React, { Component } from 'react';
export default class About extends Component {
    render() {
        return (

            <section id="about">
                <div className="row">
                    <div className="three columns">
                        <img className="profile-pic" src="images/profilepic.jpg" alt="" />
                    </div>
                    <div className="nine columns main-col">
                        <h2>Propósito</h2>
                        <p>
                            Diseñar una estructura de proyecto de tipo cooperativo y colaborativo de integración institucional, para brindar acompañamiento 
                            a las comunidades indígenas como agentes activos del control de su territorio.
                        </p>
                    </div>
                </div>
            </section>
        );
    }
}