import React, { Component } from 'react';
import './cards.css';
export default class Gestores extends Component {
    render() {
        return (

                    <div className="card">
                        <h1 className="titulo">{this.props.nombre }</h1>
                        <img src={this.props.url}  alt=''  />                     
                        <p>{this.props.descripcion }</p>
                    </div>
        );
    }
}
